import React from 'react'
import { connect } from 'react-redux'

import './user-votes.css'
import { removeFromRoom } from '../../actions/room'

class UserVotes extends React.Component {
  constructor (props) {
    super(props)

    this.state = {
      hovering: {

      }
    }

    this.renderAvatars = this.renderAvatars.bind(this)
  }

  render () {
    return (
      <React.Fragment>
        <div className='avatar-container'>
          {this.props.users && this.renderAvatars()}
        </div>
      </React.Fragment>
    )
  }

  renderAvatars () {
    return this.props.users.map((user) => {
      const image = user && user.avatarUrl ? user.avatarUrl : 'data:image/png;base64,' // TODO: Replace default
      return (
        <div className='avatar-wrapper'>
          <div
            key={`avatar: ${user.username}`}
            className={`ava${this.props.userVotes[user.username] === 'voted' ? ' voted' : ''}`} >
            <img
              className={this.state.hovering ? 'hovering' : ''}
              onMouseEnter={() => {
                this.setState({
                  hovering: {
                    ...this.state.hovering,
                    [user.username]: true
                  }
                })
              }}
              onMouseLeave={() => {
                this.setState({
                  hovering: {
                    ...this.state.hovering,
                    [user.username]: false
                  }
                })
              }}
              onClick={() => {
                removeFromRoom(this.props.roomId, user.username)
              }}
              src={this.state.hovering[user.username] ? 'https://meic.org/wp-content/uploads/2017/04/red-x.png' : image} />
          </div>
          <p>{user.username} {this.props.userVotes[user.username] === 'voted' && <React.Fragment>&#10003;</React.Fragment>}</p>
        </div>
      )
    })
  }
}

const mapStateToProps = (state) => {
  const room = state.room
  return {
    users: room.users,
    userVotes: room.gameState && room.gameState.userVotes,
    roomId: room.roomId
  }
}

export default connect(
  mapStateToProps,
  null
)(UserVotes)
